/*
  Navbar Style
*/
header {
  background: transparent;
  position: relative;
  width: 100%;
}

.nav-wrapper {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1200px;
  padding: 7px 0;
  position: relative;
  width: 100%;
}

.name a {
  color: #fff;
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  line-height: 1rem;
  pointer-events: none;
  text-decoration: none;
}

.menu li {
  display: inline-block;
  margin-bottom: 0;
  padding: 0 10px;
}

.menu li a {
  border-bottom: 1px solid transparent;
  color: white;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  line-height: 3rem;
  padding-bottom: 3px;
  text-decoration: none;
  transition: 0.3s;
}

.menu li a:hover {
  border-bottom: 1px solid #fff;
  color: #fff;
  text-decoration: none;
}

/*
  Sticky navbar style
*/
header.sticky {
  background: rgba(0, 0, 0, 0.85);
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateY(-200%);
  transition: 0.2s;
  z-index: 5;
}

body.scroll header.sticky {
  transform: translateY(0);
}

/*
  Responsive Mobile Navbar Style
*/
@media only screen and (max-width: 768px) {
  .name {
    padding: 15px 0 10px 15px;
  }

  .menu-trigger {
    background: url(../images/hamburger-icon.svg) no-repeat center/cover;
    display: block;
    height: 35px;
    position: absolute;
    right: 10px;
    top: 15px;
    width: 35px;
    z-index: 10;
  }

  .main-overlay {
    background: rgba(0, 0, 0, 0.72);
    bottom: 0;
    display: none;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: 100vw;
    z-index: 6;
  }

  .main-overlay:after {
    background: url(../images/close-icon.svg) no-repeat center/cover;
    content: "";
    display: block;
    height: 35px;
    position: fixed;
    right: 10px;
    top: 15px;
    width: 35px;
    z-index: 20;
  }

  .menu {
    background: rgba(0, 0, 0, 0.85);
    bottom: 0;
    left: 0;
    max-width: 300px;
    position: fixed;
    top: 0;
    transform: translate3d(-100%, 0, 0);
    transition: 0.2s;
    width: 100vw;
    z-index: 30;
    padding-bottom: 0;
  }

  nav.nav-wrapper ul li {
    display: block;
    margin-top: 20px;
    padding: 10px 0;
  }

  nav.nav-wrapper ul li a {
    font-size: 1.2rem;
    line-height: 1.4rem;
  }

  nav.nav-wrapper ul li a:hover {
    border-color: transparent;
  }

  body.menu-active .menu {
    display: block;
    transform: translate3d(0, 0, 0);
    margin-bottom: 0;
  }

  body.menu-active .main-overlay {
    display: block;
  }

  body.menu-active .menu-trigger,
  body.menu-active .sticky {
    display: none;
  }
}

/*
  Parallax Style Main
*/
.cover-main {
  background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url("../images/background-hero.jpeg") top center no-repeat/cover;
  height: 60vh;
}

.wrapper {
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
}

#scene {
  padding: 0;
  margin-top: 7.5rem;
}

#scene li.name {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 3.3rem;
  line-height: 3.5rem;
  text-align: center;
  width: 100%;
}

#scene li.title {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  line-height: 2rem;
  margin-top: 4.6rem;
  text-align: center;
  width: 100%;
}

#scene li.btn {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  margin-top: 9rem;
  text-align: center;
  width: 100%;
}

.portfolio-btn,
.contact-btn {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  display: inline-block;
  font-size: 1.1rem;
  margin: 0 auto;
  padding: 10px 20px;
  pointer-events: auto;
  text-decoration: none;
  transition: 0.3s;
}

.portfolio-btn:hover,
  .contact-btn:hover {
  background-color: #fff;
  color: black;
  text-decoration: none;
}

.portfolio-btn {
  margin-right: 2rem;
}

.contact-btn {
  margin-left: 2rem;
}

h2.sub-title {
  display: block;
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  line-height: 2.2rem;
  margin: 70px auto;
  position: relative;
  text-align: center;
}

h2.sub-title:after {
  background-color: #000;
  content: "";
  display: block;
  height: 2px;
  margin: 30px auto 0 auto;
  width: 100px;
}

/*
  Masonry.js Style Main
*/
.masonry-grid {
  position: relative;
}

.grid {
  margin: 0 auto;
}

.grid:after {
  content: "";
  display: block;
  clear: both;
}

.grid-item {
  border: 2px solid rgba(0, 0, 0, 0.5);
  float: left;
  margin-bottom: 1.4rem;
  width: 450px;
}

.wide-img {
  width: 600px;
}

.high-img {
  height: 710px;
}

img {
  max-width: 100%;
}

/*
  About Me Section
*/
section.beating-heart {
  align-items: center;
  align-self: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../images/aboutme.jpeg) center center no-repeat/cover;
  color: #fff;
  display: flex;
  height: 100vh;
  justify-content: center;
  margin-top: 4rem;
  padding: 50px 10px;
  text-align: center;
}

section.beating-heart h2 {
  color: #fff;
}

section.beating-heart h2:after {
  background-color: #fff;
}

section.beating-heart span {
  font-family: "Playfair Display", serif;
  font-size: 5rem;
}

section.beating-heart p {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  line-height: 2.2rem;
  margin-bottom: 2.5rem;
}

section.beating-heart .heart {
  animation: pulse 1s infinite;
  margin: 0 50px;
  position: relative;
  top: 25px;
  width: 7rem;
}

/*
  Heart Animation Keyframes
*/
@keyframes pulse {
  0% {
    transform: scale(1);
  }

  15% {
    transform: scale(1.25);
  }

  25% {
    transform: scale(1.15);
  }

  35% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(0.85);
  }
}

/*
  Slider Style
*/
section.slider-div p {
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
}

.slider {
  margin: 20px auto;
  text-align: center;
  padding: 20px;
  color: #fff;
}

.slide {
  max-height: 400px;
  max-width: 400px;
  padding: 15px;
}

.slick-prev,
.slick-next {
  background: #000;
  width: 40px;
  height: 40px;
  z-index: 1;
}

.slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
  background-color: #000;
}

.slick-prev {
  left: 0;
}

.slick-next {
  right: 0;
}

/*
  Contact Me Form
*/
/*
  Footer Style
*/
footer {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../images/footer_background.jpeg) center center no-repeat/cover;
  padding-top: 50px;
  height: 200px;
  color: #fff;
  text-align: center;
}

footer p,
  footer span {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  line-height: 1.1rem;
}

/*
  Responsive media queries
*/
@media only screen and (max-width: 930px) {
  .name a {
    font-size: 1.7rem;
    margin-left: 10px;
  }

  #scene li.name {
    font-size: 2.3rem;
    line-height: 2.5rem;
  }

  #scene li.title {
    font-size: 2rem;
    line-height: 2.2rem;
  }

  section.beating-heart p {
    font-size: 1.7rem;
    line-height: 1.8rem;
  }

  section.beating-heart .heart {
    width: 5.5rem;
    margin: 0 30px 0 30px;
  }

  section.beating-heart span {
    font-size: 3.5rem;
  }
}

@media only screen and (max-width: 675px) {
  section.beating-heart .heart {
    width: 4.5rem;
    margin: 0 30px 0 30px;
  }

  section.beating-heart span {
    font-size: 3rem;
  }

  footer {
    height: 180px;
    padding-top: 40px;
  }

  footer p,
    footer span {
    font-size: 0.9rem;
    line-height: 1.1rem;
  }

  .webicon.large {
    width: 36px;
    height: 36px;
  }
}

@media only screen and (max-width: 625px) {
  .high-img {
    height: initial;
  }

  .grid-item {
    width: initial;
  }

  section.beating-heart {
    height: 70vh;
    margin-top: 2rem;
    padding: 30px 10px;
  }

  section.beating-heart p {
    font-size: 1.4rem;
    line-height: 1.6rem;
  }

  section.beating-heart span {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 560px) {
  .cover-main {
    height: 80vh;
  }

  .menu {
    max-width: 200px;
  }

  #scene li.name {
    font-size: 2rem;
    line-height: 2.2rem;
  }

  #scene li.title {
    font-size: 1.7rem;
    line-height: 1.9rem;
  }

  #scene li.btn {
    margin-top: 8.4rem;
  }

  .portfolio-btn,
  .contact-btn {
    margin: 0.5rem;
    font-size: 1rem;
    max-width: 200px;
  }

  h2.sub-title {
    font-size: 1.7rem;
    line-height: 1.9rem;
    margin: 30px auto;
  }

  section.beating-heart {
    height: initial;
  }

  footer {
    height: 150px;
    padding-top: 20px;
  }

  footer p,
    footer span {
    font-size: 0.8rem;
    line-height: 1.1rem;
  }

  .webicon.large {
    width: 32px;
    height: 32px;
  }
}

@media only screen and (max-width: 470px) {
  #scene li.name {
    font-size: 1.5rem;
    line-height: 1.6rem;
  }

  #scene li.title {
    font-size: 1.3rem;
    line-height: 1.4rem;
  }

  section.beating-heart p {
    font-size: 1.3rem;
    line-height: 1.4rem;
  }

  section.beating-heart span {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 420px) {
  .name a {
    font-size: 1.5rem;
    line-height: 1.2rem;
  }

  #scene li.name {
    font-size: 1.3rem;
    line-height: 1.3rem;
  }

  #scene li.title {
    font-size: 1.2rem;
    line-height: 1.3rem;
  }

  .portfolio-btn,
  .contact-btn {
    font-size: 0.8rem;
  }

  h2.sub-title {
    font-size: 1.5rem;
    line-height: 1.7rem;
  }

  section.beating-heart p {
    font-size: 1.3rem;
    line-height: 1.4rem;
  }

  section.beating-heart .heart {
    width: 3rem;
    margin: 0 10px;
    padding-bottom: 10px;
  }

  section.beating-heart span {
    font-size: 1.5rem;
  }

  section.slider-div p {
    font-size: 1.2rem;
    line-height: 1.3rem;
  }
}
