/* Webicons by Adam Fairhead: http://adamfairhead.com */

.webicon {
  display: inline-block;
  background-size: 100%;
  width: 30px;
  height: 30px;
  text-indent: -999em;
  text-align: left;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  margin-bottom: 5px;
}
.webicon:hover {
  margin: -1px 0 1px;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.25);
}
.webicon.small {
  width: 20px;
  height: 20px;
  border-radius: 3px;
}
.webicon.large {
  width: 48px;
  height: 48px;
  border-radius: 6px;
}

/* restore webicons accessibility for print */
@media print {
  * {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  .webicon {
    text-indent: 0;
    width: auto !important;
    height: auto !important;
    border-radius: 0 !important;
  }
}

// Include or remove the icons you want to use on your site from this list.
$webicons-icons: f500px aboutme adn android apple behance bitbucket blogger
  branch coderwall creativecloud deviantart dribbble dropbox evernote
  fairheadcreative facebook flickr foursquare git github goodreads google
  googleplay googleplus hangouts html5 icloud indiegogo instagram instapaper
  kickstarter klout lastfm linkedin mail medium mixi msn openid picasa pinterest
  pocketapp potluck quora orkut mercurial rdio reddit renren rss skitch skype
  soundcloud spotify stackoverflow stumbleupon svtle svn tent tripadvisor tumblr
  twitter viadeo vine vimeo weibo windows wordpress xing yahoo yelp youtube
  youversion zerply;

// Specify the path to the webicons directory here. Include trailing slash.
$webicons-url: "../foundation-icons/webicons/" !default;

@each $webicons-icon in $webicons-icons {
  .no-svg .webicon.#{$webicons-icon} {
    background: url("#{$webicons-url}webicon-#{$webicons-icon}-m.png");
  }
  .no-svg .webicon.#{$webicons-icon}.large {
    background: url("#{$webicons-url}webicon-#{$webicons-icon}.png");
  }
  .no-svg .webicon.#{$webicons-icon}.small {
    background: url("#{$webicons-url}webicon-#{$webicons-icon}-s.png");
  }
  .svg .webicon.#{$webicons-icon} {
    background: url("#{$webicons-url}webicon-#{$webicons-icon}.svg");
  }
}
